/***************************************/
/* BASIC SETUP */
/***************************************/

* , *:before, *:after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body {
    background-color:  #fff;
    font-family: 'Lato', 'Arial', sans-serif;
    font-weight: 300;
    font-size: 20px;
    text-rendering: optimizeLegibility;
    max-width: 1650px;
    width: 100%;
    padding: 0;
    margin-right: auto;
    margin-left: auto;
    overflow-x: hidden;
}

/*class used to clear float after an element. Used on the parent element to remove from child elements. */
.clearfix {zoom: 1}
.clearfix:after {
    content: '.';
    clear: both;
    display: block;
    height: 0;
    visibility: hidden;
}

/***************************************/
/* REUSABLE COMPONENTS */
/***************************************/

section {
    padding: 80px 0px;
}

/* ----- HEADINGS ----- */
h1, h2, h3 {

    font-weight: 280;
    text-transform: uppercase;
}

h1 {
    margin-top: 0;
    margin-bottom: 20px;
    font-size: 240%;
    word-spacing: 4px;
    letter-spacing: 1px;
}

h2 {
    word-spacing: 2px;
    text-align: center;
    margin-bottom: 20px;
    letter-spacing: 1px;
}

h2:after {
    display: block;
    height: 2px;
    background-color: #2c80b8;
    content: "";
    width: 250px;
    margin: 0 auto;
    margin-top: 30px;
}
/***************************************/
/* HEADER */
/***************************************/
header {
    background-image: linear-gradient(rgba(0, 0, 0, 0.400), rgba(0, 0, 0, 0.400)), url(img/profile-pic.jpg);
    background-size: cover;
    background-position: center;
    height: 100vh;
    background-attachment: fixed;
}
.main-nav {
    list-style: none;
    text-align: right;
    margin-right: 35px;
}

.main-nav li {
    display: inline-block;
    padding: 30px;
}

.main-nav li a:link,
.main-nav li a:visited {
    text-decoration: none;
    color: #fff;
    font-size: 90%;
    text-transform: uppercase;
    font-weight: bold;
    border-bottom: 4px solid transparent;
    transition: border-bottom 0.2s;
    padding: 8px 0px;
    pointer-events: auto;
}

.main-nav li a:hover,
.main-nav li a:active {
    border-bottom: 4px solid #2c80b8;
    color: #2c80b8
}
[data-aos="sticky"].aos-animate{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    background-color: #cecaca;
    box-shadow: 0 2px 2px #cecaca;
    z-index: 9999;
    line-height: .25;
    transition-duration: .1s;
}
[data-aos="sticky"].aos-animate .main-nav li a:link,
[data-aos="sticky"].aos-animate .main-nav li a:visited{
    color: #000;
}
.mobile-nav-icon{
    font-size: 200%;
    color: #fff;
    float: right;
    margin-top: 30px;
    cursor: pointer; /*element is not a link element*/
    display: none;
    pointer-events: auto;
}
.logo{
    height: 100px;
    float: left;
    pointer-events: none;
}
[data-aos="sticky"].aos-animate .logo{
    height: 60px;
    pointer-events: auto;
}
.head-txt-box {
    color: #ecf0f1;
    position: absolute;
    width: 80%;
    padding: 0 5%;
    top: 80%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin-left: 5%;
}
.head-txt-box h1 {
    margin-bottom: 15px;
}
.head-txt-box p {
    font-size: 150%;
    margin-bottom: 8px
}
.head-txt-box i {
    font-size: 250%;
    margin-right: 20px;
}

.ion-logo-github {
    color: #000;
}
.ion-logo-linkedin {
    color: #0077B5;
}
.ion-md-mail {
    color: #b10606
}
/***************************************/
/* EXP */
/***************************************/
.sect-exp{
    background-color: #ecf0f1;
    text-align: center;
}
.work-exp{
    display: flex;
    /*justify-content: center;*/
    margin-bottom: 5%;
}
.col.work-pic{
    width: 250px;
}
.col.work-pic img {
    width: 100%;
    border-radius: 50px;
    margin-left: 15px;
    min-width: 135px;
}
.col.work-detail{
    margin: 0 15% 0 5%;
    text-align: left;
    line-height: 1.5;
}
.work-company{
    margin-bottom: 10px;
}
.work-company a{
    text-decoration: none;
    color: #2c80b8
}
.work-location{
    margin-bottom: 10px;
    color: #d2600c /*6D4C41*/
}
.work-title{
    margin-bottom: 5px;
    font-weight: bold;
    font-size: 16px;
    white-space: nowrap;
}
.work-desc{
    max-width: 550px;
    font-size: 16px;
    line-height: 1.5;
}
/***************************************/
/* BIO */
/***************************************/
 .sect-bio {
    color: #fff;
    background-color: #3498db;
}
.sect-bio h2:after{
    background-color:#000;
}
.bio-container {
    text-align: center;
}
.bio-container p {
    text-align: left;
    font-size: 18px;
    padding: 1%;
}
.bio-quote {
    font-size: 25px;
    margin-bottom: 15px;
}
.bio-quote:before,
.bio-quote:after{
    content: " ";
    display: inline-block;
    height: 2px;
    background-color: #000;
    width: 100px;
    margin-bottom: 5px;
}
.bio-quote::before{
    margin-right: 10px;
}
.bio-quote::after{
    margin-left: 10px;
}
/***************************************/
/* PROJECTS */
/***************************************/
.sect-projects{
    background-color: #ecf0f1;
}
.proj-row{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.proj-col{
    width: 30%;
    border: 5px solid gray;
    margin: 10px;
}
.proj-col:last-of-type img{
    height: auto;
    cursor: default;
}
.proj-col img {
    width: 100%;
    height: 100%;
    padding: 5px;
    cursor: pointer;
}
/***************************************/
/* MODAL */
/***************************************/
.modal{
    position: fixed;
    z-index: 1050;
    top: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    visibility: hidden;
    transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;
}
.show-modal{
    visibility: visible;
    transform: scale(1.2);
    opacity: 1;
    transition: visibility 0s linear 0s, opacity 0.25 0s, transform 0.25s;
}
.modal-content{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fdfdfd;
    text-align: center;
    border-radius: 0.75rem;
    max-width: 500px;
    width: 70%;
}
.item{
    display: none;
}
.active{
    display: block;
}
.item img{
    width: 80%;
}
.carousel-arrows{
    opacity: .5;
    height: 100%;
    position: absolute;
    top: 0%;
    font-size: 30px;
    width: 10%;
}
.carousel-arrows:hover{
    opacity: .9;
}
.prev,
.next{
    top: 50%;
    position: relative;
    background: transparent;
    border: none;
}
.carousel-left{
    left: 0px;
    border-top-left-radius: 0.75rem;
    border-bottom-left-radius: 0.75rem;
    background-image: linear-gradient(to left,rgba(0,0,0,.0001) 0,rgba(0,0,0,.5) 100%);
}
.carousel-right{
    right: 0px;
    border-top-right-radius: 0.75rem;
    border-bottom-right-radius: 0.75rem;
    background-image: linear-gradient(to right,rgba(0,0,0,.0001) 0,rgba(0,0,0,.5) 100%);
}
.dot{
    background-color: #fff;
    width: 15px;
    height: 15px;
    cursor: pointer;
    border-radius: 50px;
    border: #ccc 2px solid;
}
.dot-active{
    background-color: gray;
}
.dot:hover{
    background-color: lightgray;
}
/***************************************/
/* Contact */
/***************************************/
.sect-contact{
    background-color: #fff;
}
.sect-p{
    text-align: center;
    font-size: 25px;
    display: inline-flex;
    margin-right: 10%;
    margin-left: 10%;
    max-width: 80%
}
.sect-p::before,
.sect-p::after{
    content: " ";
    position: relative;
    margin-top: 45px;
    height: 2px;
    width: 200px;
    top: 50%;
    background-color: #2c80b8;
}
.contact-links{
    text-align: center;
    font-size: 100px;
}
/***************************************/
/* Queries */
/***************************************/
@media only screen and (max-width: 700px){
    .work-exp{
        flex-wrap: wrap;
    }
    .sect-exp{
        text-align: left;
    }
    .col.work-pic{
        width: 20%;
    }
    .col.work-pic img{
        margin-left: 0;
    }
    .col.work-detail{
        margin: 0;
    }
    .work-title{
        white-space: normal;
    }
}
@media only screen and (max-width: 750px){
    .main-nav{
        display: none;
        float: left;
        margin-top: 25px;
        margin-left: 25px;
    }
    .main-nav li{
        display: block;
        padding: 15px 0;
        font-size: 100%;
    }
    .mobile-nav-icon{
        display: inline-block;
    }
    [data-aos="sticky"].aos-animate .mobile-nav-icon{
        margin-top: 10px;
        color: #555;
    }
    [data-aos="sticky"].aos-animate .main-nav{
        padding: 10px 0px; /*adds space for underline on hover*/
        margin-top: 1%;
    }
}
@media only screen and (max-width: 1200px){
    .row { padding: 0 2%; }
}
@media only screen and (min-width: 0px) and (max-width: 1050px){
    .proj-col{
        width: 45%;
        min-width: 350px;
    }
}
@media only screen and (max-width: 868px){
    .bio-quote:before,
    .bio-quote:after{
        width: 50px;
    }
}
@media only screen and (max-width: 685px){
    .bio-quote:before,
    .bio-quote:after{
        width: 0;
    }
    .head-txt-box{
        margin-left: 0;
    }
}